    /**
     * plugin
     */
    var pluginLayout = function(plugin, d, parent) {
        var xPos = d.xPos, 
            yPos = d.yPos, 
            x = 0, 
            y = 0,
            bBox = parent.node().getBBox(),
            w = bBox.width, 
            h = bBox.height, 
            rh = plugin.actualHeight(), 
            rw = plugin.actualWidth();

        if (xPos == "left")
            x = -w/2;
        else if (xPos == "center")
            x = 0;
        else if (xPos == "right")
            x = w/2;
        else if(!isNaN(xPos))
            x = parseFloat(xPos);

        if (yPos == "top")
            y = -h/2;
        else if (yPos == "middle")
            y = 0;
        else if (yPos == "bottom")
            y = h/2;
        else if(!isNaN(yPos))
            y = parseFloat(yPos);
            
        if(plugin instanceof Label) {
        	if (xPos == "left")
                x -= rw;
            else if (xPos == "center")
                x -= rw/2;
                
        	if (yPos == "top")
                y -= rh;
            else if (yPos == "middle")
                y -= rh/2;
        }

        plugin.transform("translate(" + [x-parent.x(), y-parent.y()] + ")");
    };
    
    /**
     * 
     */
    var isInsideHotArea = function(x, y, box) {
        if(arguments.length === 0) {
            var axisHotArea = self.axisHotArea.node(), 
                box = axisHotArea.getBBox(), 
                mousePos = MouseUtil.mouse(axisHotArea, self.graph()), 
                x = mousePos[0],
                y = mousePos[1];
        }
        return !(x < 0 || x > box.width || y < 0 || y > box.height);
    }
    
    /**
     * xitem
     * @x x
     * @data item
     */
    var getItemByX = function(x, data) {
        var d, leftItem, rightItem, item;
        for(var i = 0, len = data.length; i < len; i++) {
            d = data[i];
            if(x >= d.x) {
                if(!leftItem) {
                    leftItem = d;
                } else if(leftItem.x < d.x) {
                    leftItem = d;
                }
            } else if(x <= d.x) {
                if(!rightItem) {
                    rightItem = d;
                } else if(rightItem.x > d.x) {
                    rightItem = d;
                }
            }
        }
        if(leftItem && rightItem) {
            item = x < leftItem.x + (rightItem.x - leftItem.x) / 2 ? leftItem : rightItem;
        } else if(leftItem && !rightItem) {
            item = leftItem;
        } else if(!leftItem && rightItem) {
            item = rightItem;
        } else {
            item = null;
        }
        return item;
    };
    
    /**
     * yitem
     * @y y
     * @data item
     */
    var getItemByY = function(y, data) {
        var d, topItem, bottomItem, item;
        for(var i = 0, len = data.length; i < len; i++) {
            d = data[i];
            if(y >= d.y) {
                if(!topItem) {
                    topItem = d;
                } else if(topItem.y < d.y) {
                    topItem = d;
                }
            } else if(y <= d.y) {
                if(!bottomItem) {
                    bottomItem = d;
                } else if(bottomItem.y > d.y) {
                    bottomItem = d;
                }
            }
        }
        if(topItem && bottomItem) {
            item = y < topItem.y + (bottomItem.y - topItem.y) / 2 ? topItem : bottomItem;
        } else if(topItem && !bottomItem) {
            item = topItem;
        } else if(!topItem && bottomItem) {
            item = bottomItem;
        } else {
            item = null;
        }
        return item;
    };
    
    /**
     * xitem
     * @x x
     * @data item
     */
    var getItemsByX = function(x, data) {
    	var item = getItemByX(x, data),
    	    items = [];
        //xitem
        if(item) {
        	var resultX = item.x;
        	for(var i = 0, len = data.length; i < len; i++) {
        		d = data[i];
        		if(resultX === d.x) {
        			items.push(d);
        		}
        	}
        }
        return items;
    };
    
    /**
     * yitem
     * @y y
     * @data item
     */
    var getItemsByY = function(y, data) {
       var item = getItemByY(y, data),
           items = [];
        //yitem
        if(item) {
            var resultY = item.y;
            for(var i = 0, len = data.length; i < len; i++) {
                d = data[i];
                if(resultY === d.y) {
                    items.push(d);
                }
            }
        }
        return items;
    };
    
