	var mask = defs.append("mask")
        .attr("id", id)
        .attr("maskUnits", "userSpaceOnUse")
        .append("rect")
        .attr("x", x)
        .attr("y", y)
        .attr("width", width)
        .attr("height", height)
        .attr("rx", rx)
        .attr("ry", ry)
        .attr("fill", "#fff");
        
